import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, Input, useStyleConfig
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import AddressSearch from '../../../../components/AddressSearch';
import { submitKyb, updatebusiness } from '../../../../store/middlewares/business';
import { apiErrorHandler } from '../../../../utils/general';

/**
 * Kyb Business Address - Component for collecting the address details of the business.
 * @module KYB
 * @returns {JSX.Element}
 */
const BusinessAddress = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();

  const style:any = useStyleConfig('FormControl', {
    variant: 'primary'
  });

  const business = useAppSelector((store) => store.business.selectedBusiness, shallowEqual);

  const [address, updateAddress] = useState({
    line1: '',
    line2: '',
    city: '',
    state: '',
    country: '',
    postalCode: '',
    addressType: 'legal'
  });

  /**
   * validation memo
   */
  const isValid = useMemo(() => (
    address.line1
      && address.city
      && address.state
      && address.country
      && address.postalCode
      && address.line1.length !== 0
      && address.city.length !== 0
      && address.state.length !== 0
      && address.country.length !== 0
      && address.postalCode.length !== 0
  ), [address]);

  useEffect(() => {
    if (business?.address) {
      updateAddress(business.address);
    }
  }, []);

  const submit = useCallback(() => {
    if (business?.id && isValid) {
      dispatch(updatebusiness(business?.id, {
        address
      })).then(() => {
        dispatch(submitKyb(business.id))
          .then(() => {
            navigate('/ao/kyb/review');
          })
          .catch(apiErrorHandler);
      }).catch(apiErrorHandler);
    }
  }, [address, business, isValid]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          heading={t('business_address.title')}
          footer={(
            <Button data-testid="submit-btn" width="100%" variant="primary" type="submit" disabled={!isValid}>
              {t('business_address.ctaText')}
            </Button>
          )}
        >
          <Box width="100%">
            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('business_address.address1')}
              </FormLabel>
              <AddressSearch
                googleApiKey={process.env.REACT_APP_GMAP_KEY || ''}
                address={address || undefined}
                placeholder={t('business_address.address1')}
                onChange={(a) => {
                  updateAddress({
                    ...a,
                    addressType: 'legal',
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('business_address.address2')}
              </FormLabel>
              <Input
                data-testid="address-line2"
                value={address.line2}
                onChange={(e) => {
                  updateAddress({
                    ...address,
                    line2: e.target.value,
                  });
                }}
                placeholder={t('business_address.address2')}
                variant="primary"
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('business_address.addressCity')}
              </FormLabel>
              <Input
                data-testid="address-city"
                value={address.city}
                onChange={(e) => {
                  updateAddress({
                    ...address,
                    city: e.target.value,
                  });
                }}
                placeholder={t('business_address.addressCity')}
                variant="primary"
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('business_address.addressState')}
              </FormLabel>
              <Input
                value={address.state}
                data-testid="address-state"
                onChange={(e) => {
                  updateAddress({
                    ...address,
                    state: e.target.value,
                  });
                }}
                placeholder={t('business_address.addressState')}
                variant="primary"
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('business_address.addressZipcode')}
              </FormLabel>
              <Input
                value={address.postalCode}
                data-testid="address-zipcode"
                onChange={(e) => {
                  updateAddress({
                    ...address,
                    postalCode: e.target.value,
                  });
                }}
                placeholder={t('business_address.addressZipcode')}
                variant="primary"
              />
            </FormControl>
          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default BusinessAddress;
